.task-card {
    background: white;
    /* box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.3); */
    border: 1px solid rgba(0, 0, 0, .85);
    position: relative;
}

.task-card::before {
    content: '';
    display: block;
    padding-top: 100%;
}

.task-card--container {
    bottom: 0;
    position: absolute;
    top: 0;
}


